import React from 'react'
import './TDtail.css'

const TDtail = (props) => {
    return (
        <div className='container'>
            <img src='https://gw.alicdn.com/bao/uploaded/i3/TB1cOEQJVXXXXaqXpXXXXXXXXXX_!!0-item_pic.jpg_500x500q90.jpg_.webp'></img>
            <div className='go-back'
                onClick={()=>props.history.push('/')}>＜</div>
            <div className='tab-bottom' style={{ position: 'fixed', bottom: 0}}>
              <div className='choose-shop' className='tl' style={{display:'inline-block'}}>
                <div className='iconfont icon-dianpu' style={{display:'inline-block'}}/>
                <div className='tab-text'>店铺</div>
              </div>
              <div className='choose-tel'  className='tl'style={{display:'inline-block'}}>
                <div className='iconfont icon-fuwupingjia-kefu' style={{display:'inline-block'}}/>
                <div className='tab-text'>客服</div>
              </div>
              <div className='choose-star' className='tl' style={{display:'inline-block'}} >
                <div className='iconfont icon-shoucang' style={{color:'orange',display:'inline-block'}}/>
                <div className='tab-text'>收藏</div>
              </div>
              <div className='choose-two' style={{display:'inline-block',}}>
                <div className='cl' style={{color:'white',display:'inline-block',background:'#FFC500'}}>加入购物车</div>
                <div className='cr' style={{color:'white',display:'inline-block',background:'#FF9402'}}>立即购买</div>
              </div>
            </div>
        </div>
    )
}

export default TDtail