import React from 'react';
import {BrowserRouter as Router, Redirect, Route, Switch } from 'react-router-dom';
import Mytab from './Mytab';
import THome from './THome';
import TCar from './TCar';
import TList from './TList';
import TMy from './TMy';
import TSearch from './TSearch';
import TDtail from './TDetail';
const App = () => {
    return <Router>
        <Switch>
            <Route exact path='/' component={THome}/>
            <Route path='/car' component={TCar}/>
            <Route path='/list' component={TList}/>
            <Route path='/my' component={TMy}/>
            <Route path='/search' component={TSearch}/>
            <Route path='/detail' component={TDtail}/>
            <Route render={()=><Redirect to='/'/>}/>
        </Switch>
        <Mytab/>
    </Router>
}

export default App
