import React from 'react';
import List from './List';
import Input from './Input';

class TodoList extends React.Component{
    constructor(){
        super();
        this.state={
            inpValue:'',
            todos:[]
        }
    }

    //文本获取
    handleChange=(e)=>{
        this.setState({inpValue:e.target.value})
    }

    componentDidMount(){
        let todos = localStorage.getItem('todos');
        if(todos){
            this.setState({
                todos:JSON.parse(todos),
            })
        }
    }
    
    componentDidUpdate(preProps,preState){
        if(preState.todos!==this.state.todos){
            localStorage.setItem(
                'todos',
                JSON.stringify(this.state.todos)
            )
        }
    }

    //点击添加
    addTodo=()=>{
         if(this.state.inpValue){
             this.setState({
                 todos:[
                     {
                         title:this.state.inpValue,
                         done:true
                     },
                     ...this.state.todos
                 ]
             });
             this.setState({inpValue:''});//自动清除
         }else{
             alert("不能为空");
         }
     }

    //点击删除
    delTodo=(idx)=>{
        this.setState({
            todos:this.state.todos.filter((item,index)=>index !== idx)
        }
    )}

    //列表
    renderTodos=(done)=>{
        return  this.state.todos.map((todo,idx)=>{
         if(done == todo.done){
             return <li>
             <input onClick={()=>this.toggle(idx)} checked={done} type='checkbox'/>
             <span dangerouslySetInnerHTML = {{__html:todo.title}}></span>
             <button onClick={()=>this.delTodo(idx)}>删除</button>
         </li>
         }
      })
    }

    //TF转换
    toggle=(idx)=>{
        let newTodos = JSON.parse(JSON.stringify(this.state.todos))
        newTodos[idx].done=!newTodos[idx].done;
        this.setState({
            todos:newTodos
        })
    }

    render() {
        // let arr1 = this.state.todos.filter(item => !!item.done);
        // let arr2 = this.state.todos.filter(item => !item.done);
        return (
            <div>
                <Input addTodo = {this.addTodo} handleChange = {this.handleChange} inpValue={this.state.inpValue} todos={this.state.todos}/>
                <List renderTodos = {this.renderTodos} delTodo = {this.delTodo} inpValue={this.state.inpValue} todos={this.state.todos}/>
            </div>
        )
    }
}

export default TodoList;